kpoint) {\n    // Prints a media query based on the value\n    @media (max-width: map-get($grid-breakpoints, $breakpoint)) {\n      @content;\n    }\n  }\n\n    // If the key doesn't exist in the map\n  @else {\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\n  }\n}","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n  @import 'resources/scss/user/colors';\n  @import 'resources/scss/user/variables';\n\n\n  #second-menu {\n\n    @include respond-up(md) {\n\n      a.dropdown-toggle {\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n\n        &:after {\n          height: 5px;\n          width: 5px;\n        }\n      }\n\n      .navbar-collapse {\n        margin-left: -0.75rem;\n        margin-right: -0.75rem;\n      }\n      #navbarTogglerDemo02 {\n        > .navbar-nav {\n          height: calc(100vh - 58px);\n          overflow: auto;\n\n          li > a, li > button {\n            padding-left: 0.75rem;\n            padding-right: 0.75rem\n          }\n        }\n      }\n    }\n    .time-mobile {\n      font-weight: normal;\n      font-size: 14px;\n      line-height: 17px;\n      color: $color-dark;\n    }\n    .container {\n      //border-bottom: 1px solid #D3D3D3;\n      //border-width: medium;\n    }\n\n    .dropdown {\n      a.dropdown-toggle {\n        outline: none !important;\n\n        &:after {\n          border: solid black !important;\n          border-width: 0 2px 2px 0 !important;\n          display: inline-block !important;\n          padding: 4px !important;\n          transform: rotate(45deg);\n          transition: 0.3s transform, 0.2s border;\n        }\n\n        &.show {\n          &:after {\n            transform: rotate(225deg);\n            vertical-align: 0;\n            border: solid $color-orange !important;\n            border-width: 0 2px 2px 0 !important;\n          }\n        }\n      }\n      .dropdown-menu {\n        border-radius: 0;\n\n        padding: 0;\n        background: $color-light-gray;\n        border: none;\n        .container {\n          padding: 0;\n          background: $color-light-gray;\n          > .row {\n            padding-bottom: 12px;\n            background: $color-light-gray;\n\n            margin: 0;\n        }\n      }\n      .category-item-menu {\n        a {\n          text-decoration: none;\n          color: $color-dark;\n          font-weight: normal;\n          font-size: 16px;\n          line-height: 19px;\n          transition: .3s color;\n          .badge {\n            background: #F1F1F1 !important;\n            border-radius: 6px !important;\n            color: $color-dark !important;\n            transition: 0.3s;\n            font-weight: normal;\n            padding: 6px;\n            margin-left: 12px;\n            font-size: 18px;\n            line-height: 18px;\n          }\n          &:hover {\n            color: $color-orange;\n            .badge {\n              background: #1A1819 !important;\n              color: $color-orange !important;\n            }\n          }\n        }\n      }\n    }\n  }\n\n    @include respond-to(md) {\n    border-bottom: none;\n\n    .dropdown {\n      position: unset !important;\n\n      .dropdown-menu {\n        background: #FFFFFF;\n        .container {\n          background: #FFFFFF;\n          padding-right: var(--bs-gutter-x, 0.75rem);\n          padding-left: var(--bs-gutter-x, 0.75rem);\n\n          > .row {\n            background: #FFFFFF;\n            margin-right: calc(-0.5 * var(--bs-gutter-x));\n            margin-left: calc(-0.5 * var(--bs-gutter-x));\n        }\n      }\n\n      left: 0 !important;\n      right: 0 !important;\n      margin-top: 0;\n\n      border: none;\n      padding: 24px;\n\n      .category-item-menu {\n        a {\n          text-decoration: none;\n          color: $color-dark;\n          font-size: 18px;\n          line-height: 21px;\n          transition: .3s color;\n          &:hover {\n            color: $color-orange\n          }\n        }\n      }\n    }\n  }\n}\n\n    li {\n      a.nav-link {\n        color: $color-dark !important;\n        opacity: 1;\n\n        &:hover {\n          color: $color-dark !important;\n        }\n      }\n    }\n    .navbar-toggler {\n      border: none;\n      border-radius: 0;\n\n      .navbar-toggler-icon {\n        background-image: url('/public/images/user/burber-menu.svg') !important;\n        height: 32px;\n        width: 32px;\n      }\n    }\n  }\n","$color-orange: #F4B12F;\n$color-dark: #1A1819;\n$color-gray: #767676;\n$color-light-gray: #F1F1F1;"],"sourceRoot":""}]);
