BAEE,WAAA,CADA,UAlFJ,CAuFI,oCACE,uBAAA,CACA,wBAAA,CACA,0BArFN,CErCI,yBFkDJ,wBA8EI,mBAAA,CADA,gBArFF,CACF,CE3CI,0BFkDJ,wBAkFI,gBArFF,CACF","sourcesContent":["\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import 'resources/scss/user/_variables.scss';\n\nfooter {\n  margin-top: 100px;\n  padding-top: 24px;\n  padding-bottom: 24px;\n\n  position: absolute;\n  width: 100%;\n  left: 0;\n\n\n  background: $color-light-gray;\n  color: $color-dark;\n\n  hr {\n    margin-top: 24px;\n    margin-bottom: 24px;\n\n    border: 1px solid #D3D3D3;\n  }\n\n  .weekend {\n    .time {\n      font-weight: normal;\n      font-size: 16px;\n      line-height: 19px;\n      margin-bottom: 4px;\n      text-decoration: none;\n      color: $color-dark;\n      display: block;\n\n      @include respond-to(md) {\n        margin-bottom: 11px;\n        font-size: 18px;\n        line-height: 21px;\n      }\n      @include respond-to(lg) {\n        font-size: 24px;\n        line-height: 28px;\n        margin-bottom: 4px;\n      }\n    }\n\n    .day {\n      font-weight: normal;\n      font-size: 14px;\n      line-height: 17px;\n      margin-bottom: 0;\n      text-decoration: none;\n      color: $color-gray;\n\n      @include respond-to(md) {\n        font-size: 12px;\n        line-height: 14px;\n      }\n\n      @include respond-to(lg) {\n        font-size: 16px;\n        line-height: 18px;\n      }\n    }\n  }\n\n\n  img {\n    width: 100%;\n    height: auto;\n  }\n\n  .nav-item {\n    a {\n      color: $color-dark !important;\n      font-size: 18px !important;\n      line-height: 21px !important;\n    }\n  }\n\n  @include respond-to(md) {\n    padding-top: 48px;\n    padding-bottom: 48px;\n  }\n\n  @include respond-to(lg) {\n    margin-top: 150px;\n  }\n\n}\n","$color-orange: #F4B12F;\n$color-dark: #1A1819;\n$color-gray: #767676;\n$color-light-gray: #F1F1F1;","@import 'colors';\n\n$container-max-widths: (\n        sm: 100%,\n        md: 740px,\n        lg: 1080px,\n        xl: 1280px,\n);\n$grid-breakpoints: (\n        xs: 0,\n        sm: 576px,\n        md: 767px,\n        lg: 1200px,\n        xl: 1365px,\n);\n\n$font-family-base: 'Rubik', sans-serif;\n$breadcrumb-item-padding-x: 0.1rem;\n$breadcrumb-active-color: $color-orange;\n$breadcrumb-divider-color: $color-gray;\n\n/// Mixin to manage responsive breakpoints\n/// @author Fulliton\n/// @param {String} $breakpoint - Breakpoint name\n/// @require $breakpoints\n@mixin respond-to($breakpoint) {\n  // If the key exists in the map\n  @if map-has-key($grid-breakpoints, $breakpoint) {\n    // Prints a media query based on the value\n    @media (min-width: map-get($grid-breakpoints, $breakpoint)) {\n      @content;\n    }\n  }\n\n    // If the key doesn't exist in the map\n  @else {\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\n  }\n}\n\n@mixin respond-up($breakpoint) {\n  // If the key exists in the map\n  @if map-has-key($grid-breakpoints, $breakpoint) {\n    // Prints a media query based on the value\n    @media (max-width: map-get($grid-breakpoints, $breakpoint)) {\n      @content;\n    }\n  }\n\n    // If the key doesn't exist in the map\n  @else {\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\n  }\n}"],"sourceRoot":""}]);
